import { Link } from 'react-router-dom';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { useState } from 'react';

const Navigation = () => {
  const initialState = { home: false, projects: false };
  const [activeLink, setActiveLink] = useState(initialState);
  const onNavClick = (e) => {
    const nameClicked = e.target.id;
    setActiveLink({ ...initialState, [nameClicked]: true });
  };
  return (
    <>
      <Navbar expand='sm'>
        <Navbar.Brand className='roboto-c head-logo' href='/home'>
          Yemi's Portfolio
        </Navbar.Brand>

        <Navbar.Toggle aria-controls='basic-navbar-nav' />
        <Navbar.Collapse>
          <Nav className='nav-cs'>
            <Nav.Link
              as={Link}
              to='/'
              id='home'
              className={activeLink.home ? 'active' : ''}
              onClick={onNavClick}
            >
              <i class='fas fa-home'> </i> home
            </Nav.Link>

            <Nav.Link
              as={Link}
              to='/projects'
              id='projects'
              onClick={onNavClick}
              className={activeLink.projects ? 'active' : ''}
            >
              <i class='fas fa-tasks'></i> projects
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
};

export default Navigation;
